// https://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid
export const generateUUID = () => {
    // Public Domain/MIT
    let d = new Date().getTime(); //Timestamp
    let d2 =
        (typeof performance !== "undefined" &&
            performance.now &&
            performance.now() * 1000) ||
        0; //Time in microseconds since page-load or 0 if unsupported
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
            let r = Math.random() * 16; //random number between 0 and 16
            if (d > 0) {
                //Use timestamp until depleted
                r = (d + r) % 16 | 0;
                d = Math.floor(d / 16);
            } else {
                //Use microseconds since page-load if supported
                r = (d2 + r) % 16 | 0;
                d2 = Math.floor(d2 / 16);
            }
            return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
    );
};

export class AsyncFunctionQueue {
    _functionQueue = [];
    _functionRunningInProgress = false;

    pushFunction(fn) {
        this._functionQueue.push(fn);
        this._tryStartQueueProcessing();
    }

    _tryStartQueueProcessing = async () => {
        if (this._functionRunningInProgress || !this._functionQueue.length)
            return;

        this._functionRunningInProgress = true;
        await this._functionQueue.shift()();
        this._functionRunningInProgress = false;
        this._tryStartQueueProcessing();
    };
}
