import { PlayerStates } from "../helpers/utils";
import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/abc-store";

const ABC_GTM_ID = "GTM-PB2GX";
const AppVersion = Store.appVersion;
const gtmEnvironment = Store.useProdConfig ? "production" : "staging";

class GoogleTagManager {
    channelData = null;
    currentPlayerState = null;
    currentTime = 0;
    videoPlaybackStartTime = null;
    videoPlaybackStarted = false;
    gtmExtraProgressEventMarkers = [15, 30];
    lastRoundedElapsedTime = 0;

    GA_EVENTS = {
        PLAY: "play",
        PROGRESS: "progress",
    };

    listeners = [];
    googleIframe = null;

    constructor() {
        // load google tag manager in separate iframe, we do not want to add ABC analytics for the entirety of
        // MLE by putting it in the parent index.html, nor can we append it in the child iframe because it
        // depends on the document onLoad function. So we add a grandchild to this happy family
        this.googleIframe = document.createElement("iframe");
        this.googleIframe.setAttribute(
            "src",
            `./au.com.abc_GTM-iframe.html?gtmId=${ABC_GTM_ID}&appVersion=${AppVersion}&gtmEnvironment=${gtmEnvironment}`
        );
        this.googleIframe.width = 1920;
        this.googleIframe.height = 1080;
        this.googleIframe.style.visibility = "hidden";
        document.body.appendChild(this.googleIframe);
        if (!Store.useProdConfig) {
            this.googleIframe.contentDocument.cookie =
                "enable_analytics_staging=true; expires=Thu, 18 Dec 2028 12:00:00 UTC;";
            // original command from ABC - we remove the domain because the browser wont store the cookie on a different domain (streamotion iframe)
            // this.googleIframe.contentDocument.cookie =
            //     "enable_analytics_staging=true; expires=Thu, 18 Dec 2028 12:00:00 UTC;path=/;domain=*.abc-test.net.au";
        }
    }

    init = () => {
        this.removeEventListeners(); // in case monitoringTeardown isn't called
        this.addListeners();
    };

    addListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.streamReady$.addReceiver(
                this.onChannelDataReceived,
                true
            ),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange, true),
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.appTeardown$.addReceiver(this.removeEventListeners),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    onAppTeardown = () => {
        this.removeEventListeners();
        this.googleIframe.contentDocument.cookie =
            "enable_analytics_staging=;expires=Thu, 01 Jan 1970 00:00:01 GMT";
        this.channelData = null;
        this.currentPlayerState = null;
        this.currentTime = 0;
        this.videoPlaybackStartTime = null;
        this.videoPlaybackStarted = false;
        this.gtmExtraProgressEventMarkers = [15, 30];
    };

    onTimeUpdate = (time) => {
        // Set the video playback start time to calculate the elapsed time (video playback doesn't start at 0 seconds)
        if (this.videoPlaybackStarted && this.videoPlaybackStartTime === null) {
            this.videoPlaybackStartTime = time;
        }
        this.currentTime = time;
        // Monitor the progress events
        this.checkGTMProgressIntervals();
    };

    checkGTMProgressIntervals = () => {
        // Sends a GTM progress event at 15s, 30s and 60s once then sends an event every 60s indefinitely
        if (this.videoPlaybackStartTime !== null) {
            const elapsedTime = Math.round(
                this.currentTime - this.videoPlaybackStartTime
            );
            if (elapsedTime === this.lastRoundedElapsedTime) {
                return;
            }

            this.lastRoundedElapsedTime = elapsedTime;

            if (
                this.channelData &&
                ((this.gtmExtraProgressEventMarkers.length !== 0 &&
                    this.gtmExtraProgressEventMarkers[0] <= elapsedTime) ||
                    elapsedTime % 60 === 0)
            ) {
                try {
                    this.gtmProgressEvent(
                        this.channelData.analytics.dataLayer,
                        elapsedTime
                    );
                    if (this.gtmExtraProgressEventMarkers.length) {
                        this.gtmExtraProgressEventMarkers.shift();
                    }
                } catch {
                    console.log(
                        Error(
                            "[acc] WARNING: Google Tag Manager progress event failed."
                        )
                    );
                }
            }
        }
    };

    onChannelDataReceived = (channelData) => {
        this.channelData = channelData;
    };

    onPlayerStateChange = (playerState) => {
        // Update the player state and send a GTM play event if player changes to playing state
        const previousPlayerState = this.currentPlayerState;
        this.currentPlayerState = playerState;

        if (
            this.currentPlayerState === PlayerStates.Playing &&
            previousPlayerState !== PlayerStates.Playing &&
            this.channelData?.analytics.dataLayer
        ) {
            this.videoPlaybackStarted = true;
            try {
                this.gtmPlayEvent(this.channelData.analytics.dataLayer, 0);
            } catch {
                console.log(
                    Error(
                        "[acc] WARNING: Google Tag Manager play event failed."
                    )
                );
            }
        }
    };

    emitAnalyticsEvent = (data) => {
        if (!this.googleIframe?.contentWindow?.dataLayer) {
            return;
        }

        console.log("[acc] ### Pushing GTM event", data.event);
        this.googleIframe.contentWindow.dataLayer.push(data);
    };

    nestFromNamespace = (obj, separator = "_") => {
        const result = {
            id: "",
            contentType: "",
            contentSource: "",
            uri: "",
            program: {
                id: "",
                name: "",
            },
            episode: {
                id: "",
                name: null,
            },
            language: "",
            canonicalUrl: "",
            classification: "",
            mediaDuration: "",
            captionsAvailable: "",
            streamType: "",
            videoType: "",
            title: {
                title: "",
            },
            categories: "",
        };

        Object.keys(obj).forEach((key) => {
            // Accumulate
            let _result = result;
            const parts = key.split(separator);

            parts.forEach((namespace, index) => {
                if (index < parts.length - 1) {
                    _result[namespace] = _result[namespace] || {};
                    _result = _result[namespace];
                } else {
                    _result[namespace] = obj[key];
                }
            });
            return _result;
        });

        return result;
    };

    processAnalyticsData = (analyticsData) => {
        const keyModifiedData = Object.keys(analyticsData).reduce(
            (acc, key) => ({
                ...acc,
                [key.replace("d_", "")]: analyticsData[key],
            }),
            {
                id: "",
                contentType: "",
                contentSource: "",
                uri: "",
                program_name: "",
                program_id: "",
                episode_name: null,
                episode_id: "",
                language: "",
                canonicalUrl: "",
                classification: "",
                mediaDuration: "",
                captionsAvailable: "",
                streamType: "",
                videoType: "",
                title_title: "",
                categories: "",
            }
        );
        const parsedData = this.nestFromNamespace(keyModifiedData);

        return Object.assign(
            {},
            {
                document: {
                    ...parsedData,
                    embedded: {
                        [parsedData.uri]: parsedData,
                    },
                },
            }
        );
    };

    // Video events
    gtmVideoEvent = (data, event) => {
        const analyticsModel = this.processAnalyticsData(data.base);
        const baseModel = {
            event,
            events: data.events,
        };

        this.emitAnalyticsEvent({
            ...baseModel,
            ...analyticsModel,
        });
    };

    gtmPlayEvent = (analyticsData, time) => {
        const data = {
            base: analyticsData,
            events: {
                elapsedSeconds: time,
                uri: analyticsData.d_uri,
            },
        };

        this.gtmVideoEvent(data, this.GA_EVENTS.PLAY);
    };

    gtmProgressEvent = (analyticsData, time) => {
        const data = {
            base: analyticsData,
            events: {
                elapsedSeconds: time,
                uri: analyticsData.d_uri,
            },
        };

        this.gtmVideoEvent(data, this.GA_EVENTS.PROGRESS);
    };
}

export default new GoogleTagManager();
