import { Messenger } from "./Messenger";
import { generateUUID, PlayerStates } from "./utils";

class MessengersClass {
    // ABC specific
    streamReady$ = new Messenger(null);

    // player
    playerState$ = new Messenger(PlayerStates.Initialising);
    timeUpdate$ = new Messenger(0);
    bitrateChange$ = new Messenger(0);
    id3Metadata$ = new Messenger(null);
    playerError$ = new Messenger(null);
    timedMetadata$ = new Messenger(null);
    appHiddenWithoutAudio$ = new Messenger(false);

    // ads
    currentAd$ = new Messenger(null);

    // other
    appTeardown$ = new Messenger(undefined);
    oztamSessionId$ = new Messenger(generateUUID());
}

export const Messengers = new MessengersClass();
