class Receiver {
    teardownFunctions = [];

    constructor(action) {
        this.action = action;
    }

    addTeardown = (teardownFn) => {
        this.teardownFunctions.push(teardownFn);
    };

    callAction = (value) => this.action(value);

    unlisten = () => {
        this.teardownFunctions.forEach((teardownFn) => teardownFn());
        this.teardownFunctions = [];
    };
}

class PartialMessenger {
    receivers = [];

    constructor(messengerLink = undefined) {
        this.messengerLink = messengerLink;
    }

    addReceiver(receiverAction, immediatelySendData = false) {
        const newReceiver = new Receiver(receiverAction);

        if (this.messengerLink) {
            const linkTeardownFunction = this.messengerLink(
                newReceiver,
                immediatelySendData
            );
            newReceiver.addTeardown(linkTeardownFunction);
        }

        newReceiver.addTeardown(() => {
            this.receivers = this.receivers.filter(
                (receiver) => receiver !== newReceiver
            );
        });

        this.receivers.push(newReceiver);
        return newReceiver;
    }

    pipe(...fns) {
        const aggregateOfAll = (source) =>
            fns.reduce(
                (observableSoFar, operatorFunction) =>
                    operatorFunction(observableSoFar),
                source
            );
        return aggregateOfAll(this);
    }
}

export class Messenger extends PartialMessenger {
    initialValue;
    state;

    constructor(state) {
        super(undefined);
        this.initialValue = state;
        this.state = state;
    }

    addReceiver = (receiverAction, immediatelySendData = false) => {
        const newReceiver = super.addReceiver(receiverAction);
        if (immediatelySendData) {
            newReceiver.callAction(this.state);
        }
        return newReceiver;
    };

    send = (data) => {
        this.state = data;
        this.receivers.forEach((receiver) => {
            receiver.callAction(data);
        });
    };

    reset = () => {
        this.send(this.initialValue);
    };

    getCurrentValue = () => this.state;
}
