import {
    AsyncFunctionQueue,
    NetworkConnectionType,
    OztamEventType,
} from "../helpers/utils";

const roundPosition = (pos) => Math.round(pos * 10) / 10;

export class OzTamClient {
    timezoneOffset = Math.abs(new Date().getTimezoneOffset());

    baseUrl = "";
    oztamUserId = "";
    vendorVersion = "";
    deviceId = "";
    connectionType = NetworkConnectionType.UNKNOWN;

    streamConfig = null;

    lastStartPosition = 0;

    eventDispatcherHandler = null;
    sessionId = "<empty>";

    isLive = true;
    ptsEpochDifference = 0;
    stopRecordingEvents = false;

    eventDispatchQueue = new AsyncFunctionQueue();

    constructor(sessionId) {
        if (sessionId) {
            this.sessionId = sessionId;
        }
    }

    setSessionID = (sessionId) => {
        this.sessionId = sessionId;
    };

    init = async (staticConfig, streamConfig) => {
        this.baseUrl = staticConfig.baseUrl;
        if (staticConfig.userId) {
            this.oztamUserId = staticConfig.userId;
        }
        this.vendorVersion = staticConfig.vendorVersion;
        this.deviceId = staticConfig.deviceId;
        this.connectionType = staticConfig.connectionType;
        this.streamConfig = streamConfig;
    };

    setPtsEpochDiff = (diff) => {
        this.ptsEpochDifference = diff;
    };

    loadEvent = (position = 0) => {
        this.generateOztamEvent(OztamEventType.LOAD, position, position);
    };

    beginEvent = (timeAtBeginning) => {
        this.lastStartPosition = timeAtBeginning;
        this.generateOztamEvent(
            OztamEventType.BEGIN,
            timeAtBeginning,
            timeAtBeginning
        );
    };

    completeEvent = (position) => {
        this.generateOztamEvent(
            OztamEventType.COMPLETE,
            this.lastStartPosition,
            position
        );
        this.lastStartPosition = 0;
    };

    setLastPosition = (position) => {
        this.lastStartPosition = position;
    };

    progressEvent = (position) => {
        if (position !== this.lastStartPosition) {
            this.generateOztamEvent(
                OztamEventType.PROGRESS,
                this.lastStartPosition,
                position
            );
            this.lastStartPosition = position;
        }
    };

    adBeginEvent = (adStartTime) => {
        this.generateOztamEvent(
            OztamEventType.AD_BEGIN,
            this.lastStartPosition,
            this.lastStartPosition
        );
    };

    adCompleteEvent = (adCompleteTime) => {
        this.generateOztamEvent(
            OztamEventType.AD_COMPLETE,
            adCompleteTime,
            adCompleteTime
        );
    };

    setStopRecordingEvents = (stop) => {
        this.stopRecordingEvents = stop;
    };

    generateOztamEvent = (eventType, fromPosition, toPosition) => {
        if (this.stopRecordingEvents) return;

        const eventTimestamp = new Date();
        const event = {
            timestamp: eventTimestamp.toISOString(),
            event: eventType,
            fromPosition: 0,
            toPosition: 0,
        };

        let boundedPosition = Math.max(0, toPosition);
        boundedPosition = Math.min(fromPosition + 60, boundedPosition);
        event.fromPosition = roundPosition(fromPosition);
        event.toPosition = roundPosition(boundedPosition);

        if (this.isLive) {
            const positionDifference = event.toPosition - event.fromPosition;
            if (this.ptsEpochDifference) {
                event.toPosition = roundPosition(
                    event.toPosition + this.ptsEpochDifference
                );
                event.timestamp = new Date(
                    event.toPosition * 1000
                ).toISOString();
            } else {
                // use epoch time if pts epoch difference isn't able to be set
                event.toPosition = roundPosition(
                    eventTimestamp.getTime() / 1000
                );
            }
            event.fromPosition = roundPosition(
                event.toPosition - positionDifference
            );
        }

        const meterEvent = this.generateMeterEvent(event);
        this.sendEvent(meterEvent);
    };

    generateMeterEvent = (oztamEvent) => {
        if (!this.streamConfig) {
            console.error("video data required for ozTeam event");
            return null;
        }

        const { eventIdentificationData, streamMetadata } = this.streamConfig;

        const shouldAppendProperties = [
            OztamEventType.LOAD,
            OztamEventType.BEGIN,
        ].includes(oztamEvent.event);

        const meterEvent = {
            protocolVersion: "1.1.1",
            vendorVersion: this.vendorVersion,
            plugin: "native",
            timestamp: new Date().toISOString(),
            timezoneOffset: this.timezoneOffset,
            sessionId: this.sessionId,
            userAgent: {
                deviceType: "tv",
            },
            ...eventIdentificationData,
            events: [oztamEvent], // keep as an array of length one, matches how it used to be done
        };

        if (shouldAppendProperties) {
            meterEvent.properties = {
                connectionType: this.connectionType,
                deviceId: this.deviceId,
                demo1: this.oztamUserId,
                ...streamMetadata,
            };
        }

        return meterEvent;
    };

    sendEvent = (meterEvent) => {
        this.eventDispatchQueue.pushFunction(async () => {
            await this._dispatchMeterEvent(meterEvent);
        });
    };

    _dispatchMeterEvent = async (meterEvent) => {
        if (!meterEvent) return;

        const options = {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json;charset=UTF-8",
            },
            body: JSON.stringify(meterEvent),
        };

        console.log(
            `[acc] Oztam dispatch meter event ${
                meterEvent.events[0].event
            } from ${meterEvent.events[0].fromPosition} to ${
                meterEvent.events[0].toPosition
            } diff ${
                meterEvent.events[0].toPosition -
                meterEvent.events[0].fromPosition
            }`
        );

        try {
            const oztamRequest = await fetch(this.baseUrl, options);
            if (!oztamRequest.ok) throw oztamRequest;
        } catch (error) {
            console.error(error);
        }
    };
}
