import { Store } from "../helpers/abc-store";
import { Messengers } from "../helpers/Messengers";
import { ErrorTypes, MLEError } from "../../provider-utils";

const abcBaseUrl = Store.baseUrls.api;

export const getSourceUrl = async (channelCodesArray) => {
    let houseNumber;

    // Use the housenumber to get the matching channel
    const currentDate = new Date(); // Gets current time by default

    channelCodesArray.forEach((program) => {
        if (program.start && program.end) {
            const [programStartHours, programStartMinutes] = program.start
                .split(":")
                .map((res) => {
                    return Number(res);
                });
            const [programEndHours, programEndMinutes] = program.end
                .split(":")
                .map((res) => {
                    return Number(res);
                });

            const currentTimestamp =
                currentDate.getUTCHours() + currentDate.getUTCMinutes() / 60;
            const programStartTime =
                programStartHours + programStartMinutes / 60;
            const programEndTime = programEndHours + programEndMinutes / 60;

            if (programStartTime < programEndTime) {
                if (
                    currentTimestamp >= programStartTime &&
                    currentTimestamp < programEndTime
                )
                    houseNumber = program.houseNumber;
            } else if (programStartTime > programEndTime) {
                if (
                    (currentTimestamp > programStartTime &&
                        currentTimestamp <= 24) ||
                    (currentTimestamp < programEndTime && currentTimestamp >= 0)
                ) {
                    houseNumber = program.houseNumber;
                }
            }
        }
    });

    // Set the house number to the first available program if none were overlapping the broadcast time
    if (!houseNumber) {
        houseNumber = channelCodesArray[0]?.houseNumber;
    }

    if (!houseNumber) {
        throw new MLEError(
            "No available house number given data",
            ErrorTypes.CHANNEL_MAP
        );
    }

    const abcChannelUrl = `${abcBaseUrl}/v3/video/${houseNumber}`;
    let matchingChannelData;
    try {
        const channelRequest = await fetch(abcChannelUrl);
        if (!channelRequest.ok) {
            throw new Error();
        }
        matchingChannelData = await channelRequest.json();
    } catch (err) {
        throw new MLEError(
            "Could not get channel data for provided house number",
            ErrorTypes.PROVIDER
        );
    }
    Messengers.streamReady$.send(matchingChannelData);

    if (matchingChannelData._embedded) {
        const videoData = matchingChannelData._embedded.playlist[0];
        const sourceUrl =
            videoData.streams.mpegdash[720] ||
            videoData.streams.mpegdash[
                Object.keys(videoData["stream-labels"])[0]
            ];
        const drmLicenseKey = await getDrmToken(Store.clientId, houseNumber);
        return { sourceUrl, drmLicenseKey: drmLicenseKey };
    } else {
        throw new MLEError(
            "Could not retrieve video stream from the abc api.",
            ErrorTypes.PROVIDER
        );
    }
};

export const getDrmToken = async (clientId, houseNumber) => {
    const jwtUrl = abcBaseUrl + "/v3/token/jwt";
    const drmTokenUrl = abcBaseUrl + "/v3/token/drm/" + houseNumber;

    const clientData = "clientId=" + clientId;

    try {
        const jwtTokenResponse = await fetch(jwtUrl, {
            method: "POST",
            headers: {
                "content-type": "application/x-www-form-urlencoded",
            },
            body: clientData,
        });
        const jwtToken = (await jwtTokenResponse.json()).token;

        if (jwtToken) {
            const drmResponse = await fetch(drmTokenUrl, {
                headers: {
                    authorization: "Bearer " + jwtToken,
                    "content-type": "application/json; charset=utf-8",
                },
            });
            const drmLicenseKey = (await drmResponse.json()).license;

            if (drmLicenseKey) {
                return drmLicenseKey;
            } else {
                throw Error("Faild to get drm license key from abc");
            }
        } else {
            throw Error("Failed to get jwt token from abc");
        }
    } catch (err) {
        throw new MLEError(
            "Could not retrieve drm token from the abc api",
            ErrorTypes.PROVIDER
        );
    }
};
