export class ErrorTypes {
    static GENERIC = 'GENERIC';
    static AUTH = 'AUTH';
    static PROVIDER = 'PROVIDER';
    static CHANNEL_MAP = 'CHANNEL_MAP';
}

export class MLEError extends Error {
    type;
    status;

    constructor(message, type, status) {
        super(message);
        this.message = message;
        this.type = type;
        this.status = status;
    }

    getDetail() {
        return null;
    }

    getMediaFailedPayload() {
        return {
            error: {
                major: 20000,
                minor: 110, // MIPA-100
                desc: `${this.type}|${this.message}|${this.status}`,
            },
        };
    }
}

export const setTokenInDeviceStorage = async (
    storageKey,
    encodedTokenString
) => {
    // eslint-disable-next-line no-console
    console.log(`Updating token for ${storageKey}`);
    return await fetch(`http://localhost:9005/as/system/token/${storageKey}`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            token: encodedTokenString,
        }),
    });
};
