import { Store } from "./helpers/abc-store";
import { MessengerLink } from "./helpers/MessengerLink";
import { Messengers } from "./helpers/Messengers";
import { ErrorTypes, MLEError } from "../provider-utils";
import { NetworkConnectionType } from "./helpers/utils";
import { getSourceUrl } from "./abc-services/abc-api.js";
import OztamBridge from "./analytics/OztamBridge";
import GoogleTagManager from "./analytics/GoogleTagManager";
import { tryLogin } from "./abc-services/abc-login";
import { getDrmToken } from "./abc-services/abc-api.js";

export default class AbcProvider {
    config;
    messengerLink;
    videoElement;

    constructor(config) {
        this.config = config;
    }

    /**
     * @typedef {import('../../app/get-asset').GetAssetRequest} GetAssetRequest
     * @typedef {import('../../app/get-asset').GetAssetResponse} GetAssetResponse
     */
    /**
     * @param {GetAssetRequest} args requested asset and device information
     * @returns {GetAssetResponse} streams for the requested asset
     */
    async getAsset({
        authToken,
        glassAssetLocator,
        glassContentOptions,
        deviceInfo,
    }) {
        let gracenoteId = getFileName(glassAssetLocator.pathname);
        const provider = glassAssetLocator.searchParams.get("provider");
        const providerId = glassAssetLocator.searchParams.get("providerid");

        const {
            glassConfigure: { brand: devicePlatform, version: deviceOSVersion },
            uuid: deviceId,
        } = deviceInfo;
        const { connectionType: networkConnectionType } = glassContentOptions;

        console.info("[acc] ABC received startup data", {
            glassAssetLocator,
            glassAssetLocatorParsed: {
                gracenoteId,
                provider,
                providerId,
            },
            glassContentOptions,
            deviceInfo,
        });

        let channelCodesArray;
        try {
            channelCodesArray = JSON.parse(providerId);
        } catch (err) {
            throw new MLEError(
                "Could not parse ABC channel info from provider info",
                ErrorTypes.CHANNEL_MAP
            );
        }

        console.info(
            `[acc] Accedo version ${Store.appVersion}, parent app version ${deviceInfo.appVersion}, player version ${deviceInfo.playerVersion}`
        );

        Store.deviceId = deviceId;
        Store.devicePlatform = devicePlatform;
        Store.deviceFirmwareVersion = deviceOSVersion;
        if (networkConnectionType.toLowerCase() === "ethernet") {
            Store.deviceNetworkConnection = NetworkConnectionType.LAN;
        } else if (networkConnectionType.toLowerCase() === "wifi") {
            Store.deviceNetworkConnection = NetworkConnectionType.WIFI;
        }

        // dont really need anything from login so we do it at the same time
        // as getSourceUrl for startup time. Can separate out later if need be
        const [, sourceInfo] = await Promise.all([
            tryLogin(),
            getSourceUrl(channelCodesArray),
        ]);

        console.log("[acc] passing back url " + sourceInfo.sourceUrl);

        return {
            aampInitConfigOverrides: {
                useAbsoluteTimeline: false, // set to true for first party streams, breaks abc streams, need to override it here
                ...this.config?.aampInitConfigRemoteOverrides,
            },
            streams: [
                {
                    url: sourceInfo.sourceUrl,
                    drmConfig: {
                        [Store.preferredDrmSystem]: Store.licenseAcquisitionUrl,
                        preferredKeysystem: Store.preferredDrmSystem,
                    },
                    requestHeaders: [
                        {
                            key: "customdata",
                            value: sourceInfo.drmLicenseKey,
                            isLicenseRequest: true,
                        },
                    ],
                },
            ],
        };
    }

    monitoringSetup(videoElement) {
        console.log("[acc] call monitoringSetup");
        if (!this.messengerLink) {
            this.messengerLink = new MessengerLink(Messengers);
        }
        this.videoElement = videoElement;
        this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        this.messengerLink.linkVideoElementListeners(this.videoElement);

        // expect modules to destroy themselves when appTeardown$ fires
        OztamBridge.init();
        GoogleTagManager.init();
    }

    monitoringTeardown() {
        console.log("[acc] call monitoringTeardown");
        if (this.messengerLink && this.videoElement) {
            this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        }
        Messengers.appTeardown$.send();
        Object.keys(Messengers).forEach((key) => Messengers[key].reset());
    }
}

function getFileName(url) {
    if (url) {
        return url.substring(url.lastIndexOf("/") + 1, url.lastIndexOf("."));
    }

    return "";
}
